












































































import { Vue, Component } from 'vue-property-decorator';
import PackageJson from '@/package.json';

import {
    ServiceResponseFilter,
    ServiceServerConfig,
    ServiceUser,
    ServiceDialog,
    StorageUser,
    StorageRemeber,
    EUserRole,
    ServiceFormConfig,
    ServiceDatetime,
} from '@/../helpers';
import { IStorage, IComponent } from '@/../models';
import { EFormMode, EFormType } from '@/../enums';

import { ServerApiPath, WebPagePath } from '@/config';

import Forms from '@/../components/form/forms.vue';
import LoginLanguage from '@/../components/login-language/login-language.vue';
import Loading from '@/../components/loading/loading.vue';

enum ERemeberMe {
    rememberMe = 'rememberMe',
    noRemeberMe = 'noRemeberMe',
}

interface IPageItem {
    webVersion: string;
    serverVersion: string;
    showError: {
        account: boolean;
        password: boolean;
    };
}

interface IFormData {
    account: string;
    password: string;
}

interface IFormItem {
    isClearState: boolean;
}

interface IFormConfigs {
    account: IComponent.IFormConfig;
    password: IComponent.IFormConfig;
}

interface IFormButtonDisabled {
    account: boolean;
    password: boolean;
}

interface ILicenseExpiredAlmost {
    showAlert: boolean;
    edge: number;
    engine: number;
}

@Component({
    components: { LoginLanguage, Forms },
})
export default class VuePage extends Vue {
    //#region Variables & Computed
    private pageItem: IPageItem = {
        webVersion: 'v1.00.00',
        serverVersion: 'v1.00.00',
        showError: {
            account: false,
            password: false,
        },
    };

    private formItem: IFormItem = {
        isClearState: true,
    };

    private formData: IFormData = {
        account: '',
        password: '',
    };

    private formConfig: IFormConfigs = {
        account: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
        password: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
    };
    private formConfigs: IComponent.IFormConfig[] = [];

    private formButtonDisabled: IFormButtonDisabled = {
        account: true,
        password: true,
    };
    private get formSaveDisable(): boolean {
        return Object.values(this.formButtonDisabled).some((x) => x === true);
    }

    private remeberMe: string = ERemeberMe.noRemeberMe;
    private eRemeberMe: any = ERemeberMe;
    private eFormMode = EFormMode;
    private loading = new Loading();

    //#endregion

    //#region Vue Life
    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {
        this.initVersion();
    }
    private async beforeMount(): Promise<void> {}
    private async mounted(): Promise<void> {
        await this.initData();
        document.getElementById('account').focus();
    }
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}
    //#endregion

    //#region Init Data
    private async initVersion(): Promise<void> {
        this.initLoginInfo();
        await this.initWebVersion();
        await this.initServerVersion();
    }

    private initWebVersion(): void {
        this.pageItem.webVersion = `v${PackageJson.version}`;
    }

    private initLoginInfo(): void {
        let loginInfo: IStorage.IRemember = StorageRemeber.getStorage();
        this.formData.account = loginInfo.username;
        this.formData.password = loginInfo.password;
        if (loginInfo.username && loginInfo.password) {
            this.remeberMe = ERemeberMe.rememberMe;
            this.formButtonDisabled.account = false;
            this.formButtonDisabled.password = false;
        }
    }

    private async initServerVersion(): Promise<void> {
        this.pageItem.serverVersion = `v${SERVER_VERSION_RES.serverVersion}`;
    }
    //#endregion

    //#region Init Form Data
    private initData(): void {
        this.initFormConfig();
    }

    private initFormConfig(): void {
        this.formConfigs = [];

        // account
        this.formConfig.account.id = 'account';
        this.formConfig.account.label = this.$i18n.Common_Account;
        this.formConfig.account.placeholder = this.$i18n.Common_Account;
        this.formConfig.account.formType = EFormType.text;
        this.formConfig.account.required = true;

        // password
        this.formConfig.password.id = 'password';
        this.formConfig.password.label = this.$i18n.Common_Password;
        this.formConfig.password.placeholder = this.$i18n.Common_Password;
        this.formConfig.password.formType = EFormType.password;
        this.formConfig.password.required = true;

        this.formConfigs = [this.formConfig.account, this.formConfig.password];
    }
    //#endregion

    private formConfigValues(value: any, disabledButton: boolean, id: string): void {
        if (this.formData.hasOwnProperty(id)) {
            this.formData[id] = value;
        }
        if (this.formButtonDisabled.hasOwnProperty(id)) {
            this.formButtonDisabled[id] = disabledButton;
        }
    }

    private async onSubmit(event) {
        event.preventDefault();
        this.login();
    }

    private async login(): Promise<boolean> {
        if (this.remeberMe === ERemeberMe.noRemeberMe) {
            StorageRemeber.clearStorage();
        }

        if (!this.formData.account) {
            this.pageItem.showError.account = true;
            return false;
        }

        if (!this.formData.password) {
            this.pageItem.showError.password = true;
            return false;
        }

        let url: string = ServiceServerConfig.url(ServerApiPath.Login_C);
        let param: {
            username: string;
            password: string;
        } = {
            username: this.formData.account,
            password: this.formData.password,
        };

        if (this.remeberMe == ERemeberMe.rememberMe) {
            StorageRemeber.saveStorage(param);
        }

        LOGIN_RES.user.username = param.username;
        StorageUser.saveStorage(LOGIN_RES);
        ServiceUser.initVueUser();
        this.pageToHome();
    }

    private pageToHome(): void {
        this.$router.push(WebPagePath.Root);
    }

    private async logout(): Promise<boolean> {
        // Clear Session
        let user: IStorage.IUser = StorageUser.getStorage();
        StorageUser.clearStorage();
        if (!user.sessionId) {
            this.$router.push(WebPagePath.Login);
            return false;
        } else {
            // Send API to Server
            let url: string = ServiceServerConfig.url(ServerApiPath.Logout_C);
            let param: { sessionId: string } = { sessionId: user.sessionId };
            await this.axios.post(url, param).catch((e: any) => console.error('Logout Error: ', e));
            // Change page to login
            this.$router.push(WebPagePath.Login);
        }
    }

    private forgetpassword(): void {
        // Change page to forgetpassword
        this.$router.push(WebPagePath.ForgetPassword);
    }
}

