
























import { Vue, Component } from 'vue-property-decorator';

import { ServiceI18n, StorageLanguage } from '@/../helpers';
import { IComponent } from '@/../models';

import * as Rx from 'rxjs';
import * as RxOperator from 'rxjs/operators';
import { setting$ } from '@/stores';

// Component
import Multiselect from 'vue-multiselect';
import 'vue-multiselect/dist/vue-multiselect.min.css';

interface ILanguageOptions extends IComponent.ISelectionBase {
    $isDisabled: boolean;
}

@Component({
    components: { Multiselect },
})
export default class LoginLanguage extends Vue {
    //#region Variables
    private languageDefault: ILanguageOptions = {
        objectId: 'en-US',
        name: 'English',
        $isDisabled: false,
    };
    private language: ILanguageOptions = JSON.parse(JSON.stringify(this.languageDefault));
    private languageOptions: ILanguageOptions[] = [];

    private nowLanguage: string = StorageLanguage.getStorage();

    private stop$: Rx.Subject<{}> = new Rx.Subject();
    private showI18nKey: boolean = false;
    //#endregion Variables

    //#region Vue Life
    private async beforeCreate() {}
    private async created() {
        this.getIsI18nKey();
        this.initLanguage();
    }
    private async beforeMount() {}
    private async mounted() {}
    private async beforeDestroy() {}
    private async destroyed() {}
    //#endregion Vue Life

    //#region Funation
    private getIsI18nKey(): void {
        setting$.pipe(RxOperator.takeUntil(this.stop$)).subscribe({
            next: (x) => {
                this.showI18nKey = x.showI18nKey === true ?? false;
            },
        });
    }

    private initLanguage(): void {
        let self = this;
        let originalLanguage = JSON.parse(JSON.stringify(ServiceI18n.languageI18n));

        if (!this.showI18nKey) {
            delete originalLanguage['i18n-key'];
        }

        this.languageOptions = [];

        // get language options
        let storageIsSupport: boolean = false;
        let originalLanguageKeys: string[] = Object.keys(originalLanguage);
        (originalLanguageKeys || []).map((objectKey: string, index: number) => {
            let tempData: ILanguageOptions = {
                objectId: objectKey,
                name: originalLanguage[objectKey],
                $isDisabled: false,
            };

            self.languageOptions.push(tempData);

            if (this.nowLanguage === self.languageOptions[index].objectId) {
                self.languageOptions[index].$isDisabled = true;
            }

            if (this.nowLanguage && this.nowLanguage === tempData.objectId) {
                storageIsSupport = true;
                self.language = JSON.parse(JSON.stringify(tempData));
            }

            // check whether storage language is still support; if not, need to set to default language
            if (index === originalLanguageKeys.length - 1 && storageIsSupport === false) {
                let key: string = originalLanguageKeys[0];
                self.selectLanguage({
                    objectId: key,
                    name: originalLanguage[key],
                    $isDisabled: false,
                });
            }
        });
    }

    private selectLanguage(selectedOption: ILanguageOptions): void {
        let router: any = this.$router;
        if (selectedOption && selectedOption.objectId) {
            this.language = selectedOption;
            StorageLanguage.saveStorage(selectedOption.objectId);
        } else {
            this.language = JSON.parse(JSON.stringify(this.languageDefault));
        }
        ServiceI18n.setVueI18n();
        if (router && router.history && router.history.current && router.history.current.fullPath) {
            let path = JSON.parse(JSON.stringify(router.history.current.fullPath));
            this.$router.go(path);
        }

        this.nowLanguage = StorageLanguage.getStorage();
    }
    //#endregion Funation
}
